<template>
    <div>
        <ul>
            <li v-for="(item,index) in choose" v-bind:key="index" @click="change(item)">{{item}}</li>
        </ul>
        <input type="text" v-model="option" @keyup.enter="text">
    </div>
</template>

<script setup>
    import { toRef } from "vue"
import { useStore } from "vuex"
import {useRouter} from "vue-router"

const store = useStore()
const router=useRouter()
const choose = toRef(store.state.choose)
const option=toRef("")
function text() {
    choose.value.push(option.value)
}
function change(item) {
    console.log(item)
    router.push({ name: "choose", params: { option: item } })
    
}

</script>

<style  scoped>
    div {
        width: 600px;
        margin-left: 50%;
        transform: translate(-50%,0);
    }
</style>